<template>
  <div class="home">
    <van-nav-bar :fixed="true">
      <template #left>
        <div class="home-title">
          {{timeText}}
          <span v-if="isLogin">, {{userInfo.nickName}}</span>
        </div>
      </template>
      <template #right>
        <van-search placeholder="请输入商品名称" shape="round"
        @focus="gosearch" />
      </template>
    </van-nav-bar>
    <div class="banner-box">
      <van-swipe :autoplay="5000" :show-indicators="false">
        <van-swipe-item
          v-for="(item, index) in bannerData"
          :key="index"
          @click="viewProductInfo(item.pid)"
        >
          <img class="auto-img" :src="item.bannerImg" alt />
          <div class="pro-info-banner one-text">{{item.name}}</div>
        </van-swipe-item>
      </van-swipe>
    </div>
    <van-grid icon-size="40px">
      <van-grid-item v-for="(item,index) in iconsData" :key="index" :icon="item.url" :to="item.to" :text="item.text" />
    </van-grid>
    
    <div class="hot-product">
      <div class="hot-product-title">热门推荐</div>
      <div class="product-box clearfix">
        <div
          class="product-item fl"
          :class="[index % 2 == 0 ? 'p-left' : 'p-right']"
          v-for="(item, index) in hotProducts"
          :key="index"
        >
          <div class="p-img" @click="viewProductInfo(item.pid)">
            <img class="auto-img" :src="item.smallImg" alt />
          </div>
          <div class="pro-name one-text">{{item.name}}</div>
          <div class="clearfix">
            <div class="pro-price fl">￥{{item.price}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/less/home.less";
export default {
  name: "Home",
  data() {
    return {
     
      timeText: "",
      isLogin: false,
      userInfo: {},
      bannerData: [],
      hotProducts: [],
      iconsData: [
        {
          url: require("../../assets/images/icon-3.gif"),
          text: "送TA咖啡",
          to: {name: 'Menu'}
        },
        {
          url: require("../../assets/images/icon-1.gif"),
          text: "优惠券",
          to:{name:'Discount'}
        },
        {
          url: require("../../assets/images/icon-5.gif"),
          text: "签到",
          to:{name:'Qdao'}
        },
        {
          url: require("../../assets/images/icon-6.gif"),
          text: "企业账号",
          to:{name:'Corporate'}
        }
      ]
    };
  },

  created() {
    this.getTime();
    this.getbannerData();
    this.getHotProducts();
    this.getUserInfo();
  },

  methods: {
    gosearch(){
     this.$router.push({ name: "Search" });
    },
    getTime() {
      let hours = new Date().getHours();
      if (hours >= 6 && hours < 12) {
        this.timeText = "早上好";
      } else if (hours >= 12 && hours < 18) {
        this.timeText = "下午好";
      } else if (hours >= 18 && hours < 24) {
        this.timeText = "晚上好";
      } else {
        this.timeText = "深夜好";
      }
    },

    // 获取轮播图数据
    getbannerData() {
      this.$toast.loading({
        message: "加载中...",
        duration: 0,
        forbidClick: true,
        loadingType: "spinner"
      });
      this.axios({
        method: "GET",
        url: "/banner",
        params: {
          appkey: this.appkey
        }
      }).then(result => {
        this.$toast.clear();
        
        if (result.data.code == 300) {
          this.bannerData = result.data.result;
        }
      });
    },

    // 获取推荐商品的数据
    getHotProducts() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "typeProducts",
        params: {
          appkey: this.appkey,
          key: "isHot",
          value: 1
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 500) {
            this.hotProducts = result.data.result;
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });

      
    },
    viewProductInfo(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },

    // 获取用户信息
    getUserInfo() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner"
      });
      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString
        }
      }).then(result => {
        this.$toast.clear();
        
        if (result.data.code == "A001") {
          if (result.data.result.length > 0) {
            this.isLogin = true;
            this.userInfo = result.data.result[0];
            
          }
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
</style>